import React from 'react';
import Img from '!!file!../../images/noah.jpg';
import Img2 from '!!file!../../images/comp.jpg';
import {Card, CardHeader, CardMedia, CardTitle} from 'material-ui/Card';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import lightBaseTheme from 'material-ui/styles/baseThemes/lightBaseTheme';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import { styles } from './styles';

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      foo: 'bar'
    };
  }

  render() {
    return (
      <div>
        <MuiThemeProvider muiTheme={getMuiTheme(lightBaseTheme)}>
          <Card style={styles.card}>
            <CardHeader
              title="Full Stack Software Developer"
              subtitle="Front-End React.js Focus"
              avatar={Img}
            />
            <CardMedia
              overlay={<CardTitle title="Creative Minimalist Developer" subtitle="With a Love for Responsive Apps" />}
            >
              <img src={Img2} alt="" />
            </CardMedia>
          </Card>
        </MuiThemeProvider>
      </div>
    );
  }
}



